*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --black-text-color: #000;
  --white-text-color: #fff;
  --light-text-color: #8c8c8c;
  --dark-text-color: #545454;
  --accent-text-color: #ff6b08;
  --mainBGL: #e5e5e5;
  --lightBG: #f7f7f7;
  --accentBG: #ff6b01;
  --modal-overlay: rgba(0, 0, 0, .25);
  --Gap16: 16px;
  --Gap20: 20px;
  --cubic: cubic-bezier(.4, 0, .2, 1);
  --duration: 250ms;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (max-width: 767px) {
  .container {
    max-width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1280px;
  }
}

.wrapper {
  min-height: 100vh;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

html {
  scroll-behavior: smooth;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.queue-btn {
  height: 44px;
  width: 129.53px;
  border: 1px solid var(--white-text-color);
  text-transform: uppercase;
  color: var(--white-text-color);
  cursor: pointer;
  height: 44px;
  width: 129.5px;
  transition: color var(--duration) var(--cubic), background-color var(--duration) var(--cubic);
  background-color: #000;
  border-radius: 5px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
}

@media screen and (min-width: 768px) {
  .queue-btn {
    width: 152px;
  }
}

@media screen and (min-width: 1200px) {
  .queue-btn {
    width: 147px;
    height: 42px;
  }
}

.queue-btn:active {
  background-color: var(--accent-text-color);
  color: var(--white-text-color);
  border: 1px solid var(--accentBG);
  transition: color var(--duration) var(--cubic), background-color var(--duration) var(--cubic);
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
}

.queue-btn:active:hover, .queue-btn:active :focus {
  cursor: pointer;
}

.queue-btn:active:disabled {
  background-color: var(--dark-text-color);
  color: var(--black-text-color);
}

.add-queue-btn {
  height: 44px;
  width: 129.53px;
  border: 1px solid var(--black-text-color);
  text-transform: uppercase;
  color: var(--black-text-color);
  transition: color var(--duration) var(--cubic), background-color var(--duration) var(--cubic);
  transition: color var(--duration) var(--cubic), background-color var(--duration) var(--cubic);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 5px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
}

.add-queue-btn:hover, .add-queue-btn:focus {
  cursor: pointer;
  border: 1px solid var(--accentBG);
  color: var(--accentBG);
}

.add-queue-btn:disabled {
  color: var(--light-text-color);
  border: 1px solid var(--accentBG);
  height: 44px;
}

@media screen and (max-width: 767px) {
  .add-queue-btn {
    width: 110px;
  }
}

@media screen and (min-width: 768px) {
  .add-queue-btn {
    width: 125.03px;
  }
}

@media screen and (min-width: 1200px) {
  .add-queue-btn {
    width: 136px;
  }
}

body.locked {
  overflow: hidden;
}

.modal {
  width: 100%;
  height: 100vh;
  opacity: 0;
  visibility: hidden;
  background: rgba(0, 0, 0, .8);
  transition: all .8s;
  position: fixed;
  top: 0;
  left: 0;
}

.modal.active {
  opacity: 1;
  visibility: visible;
}

.modal.active .modal-body {
  opacity: 1;
  transform: translate(0);
}

.modal-bg {
  min-height: 100%;
  justify-content: center;
  align-items: center;
  padding: 30px 10px;
  display: flex;
}

.modal-body {
  color: #000;
  max-width: 800px;
  max-height: 90%;
  opacity: 0;
  background-color: #fff;
  border-radius: 16px;
  margin: auto;
  padding: 30px;
  transition: all .8s;
  position: relative;
  transform: translate(0, -100%);
}

.popup_close {
  cursor: pointer;
  text-decoration: none;
  position: absolute;
  top: 10px;
  right: 16px;
}

.popup__title {
  margin: 0 0 1em;
  font-size: 40px;
}

.login-modal__backdrop {
  z-index: 100;
  width: 100%;
  height: 100%;
  opacity: 1;
  transition: opacity var(--duration) var(--cubic);
  background-color: var(--modal-overlay);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.login-modal__backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.login-modal__backdrop.is-hidden .login-modal {
  transform: translate(-50%, -50%)scale(.7);
}

.login__modal {
  transition: transform var(--duration) var(--cubic);
  background-color: var(--lightBG);
  border: 2px solid var(--accentBG);
  padding: var(--Gap20);
  min-width: 280px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

@media screen and (min-width: 768px) {
  .login__modal {
    min-width: 380px;
  }
}

@media screen and (min-width: 1200px) {
  .login__modal {
    min-width: 450px;
  }
}

